<template>
  <div id="app">
    <h1>在线翻译</h1>
    <h5 class="text-muted">简单/易用/便捷</h5>
    <translateInput v-on:subForm="getForm"></translateInput>
    <!-- 这里的submitForm值子组件传递过来注册的事件 -->
    <translateOnput v-text="translatedText"></translateOnput>
  </div>
</template>

<script>
import translateInput from './components/translateInput'
import translateOnput from './components/translateOnput'
export default {
  name: 'App',
  data(){
    return {
       translatedText:''
    }
  },
  components:{
    translateInput,translateOnput
  },
  methods:{
    //这个的text是接收子组件传递过来的参数
    getForm(val,language){
       this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20181031T150256Z.2d9600ff72c3a31c.c2ca9a5a6a563c5d8dd482b8d7527e7c5657da1c&lang="+language+"&text="+val)
       .then((response)=>{
        //  alert(response.body.text[0]);
        this.translatedText = response.body.text[0];
       })
    }
  }
}
</script>

<style scoped>
body{
  background-color: #fff;
}
#app{
  background-color:aliceblue;
  color: #333;
  
  position: relative;
  left: 50%;
  top: 50%;
  height: 500px;
  width: 1000px;
  margin-left:-500px;
  margin-top: 100px;
}
h1,h5{
   text-align: center;
   font-weight: 300;
}
</style>



