<template>
  <div id="translateInput" >
        <form v-on:submit="submitForm">
          <input class="" type="text" v-model="value" placeholder="请输入翻译内容">
          <select v-model="language">
              <option value="en">English</option>
              <option value="ru">Russian</option>
              <option value="ko">korean</option>
              <option value="ja">janpenese</option>
          </select>
          <button type="submit">翻译</button>
        </form>
  </div>
</template>

<script>

export default {
  name: 'translateInput',
  data(){
      return{
        value:"",
        language:"en",
      }
  },
  methods:{
     submitForm(e){
         this.$emit('subForm',this.value,this.language);
         //注册的事件，并且带上参数
        e.preventDefault();
     }
  }
}
</script>

<style scoped>
form{
  text-align: center;
}
input{
  padding: 20px;
  background: rgb(255,255,255,0.8);
  width: 400px;
  height: 10px;
  font-weight: 300;
  border: 1px dotted #fff;
}
select{
  background: rgb(255,255,255,0.8);
  padding: 12px;
  border: 1px dotted #fff;
}
option{
  font-weight:300px;
  padding: 2px;
}
button{
  padding: 10px;
  background:rgb(255,200,200,0.1);
}
</style>



